import React, { Suspense } from 'react';
import { loading } from '../../../extensions';
import { Redirect, Switch, RouteComponentProps, useLocation } from 'react-router-dom';
import { useSelector } from 'react-redux';
import LayoutRoute from './LayoutRoute';
import './Layout.scss';

// Newsfeed
const NewsfeedIntro = React.lazy(() => import('../newsfeed/NewsfeedIntro'));
const SelectCategory = React.lazy(() => import('../newsfeed/SelectCategory'));
const SelectCategorySearch = React.lazy(() => import('../newsfeed/SelectCategorySearch'));
const ViewArticles = React.lazy(() => import('../newsfeed/ViewArticles'));
const Favorite = React.lazy(() => import('../newsfeed/Favorite'));

// Mobility
const MobilityIntro = React.lazy(() => import('../mobility/MobilityIntro'));
const MobilitySearch = React.lazy(() => import('../mobility/MobilitySearch'));
const MobilityView = React.lazy(() => import('../mobility/MobilityView'));
const MobilityDetail = React.lazy(() => import('../mobility/MobilityDetail'));
const MobilitySaved = React.lazy(() => import('../mobility/MobilitySaved'));
const Hashtag = React.lazy(() => import('../mobility/Hashtag'));
const PostureType = React.lazy(() => import('../mobility/PostureType'));
const AllTrainers = React.lazy(() => import('../mobility/AllTrainers'));
const TrainerDetail = React.lazy(() => import('../mobility/TrainerDetail'));

// Profile
const Profile = React.lazy(() => import('../personalInformation/Profile'));
const ChangePassword = React.lazy(() => import('../personalInformation/ChangePassword'));
const UpdateBio = React.lazy(() => import('../personalInformation/UpdatePersonalInformation'));
const NurseDocuments = React.lazy(() => import('../personalInformation/Documents'));
const NurseFeedbacks = React.lazy(() => import('../personalInformation/Feedbacks'));

// Consulting
const Consulting = React.lazy(() => import('../consulting/ConsultingMainPage'));
const AddFriendMainPage = React.lazy(() => import('../consulting/AddFriendMainPage'));
const SearchFriend = React.lazy(() => import('../consulting/SearchFriend'));
const FriendRequest = React.lazy(() => import('../consulting/FriendRequest'));
const InformationFriend = React.lazy(() => import('../consulting/InformationFriendPage'));
const AllAppointmentsMainPage = React.lazy(() => import('../consulting/allAppointments'));
const ViewAppointments = React.lazy(() => import('../consulting/allAppointments/ViewAppointments'));
const ScheduleWebinar = React.lazy(() => import('../consulting/ScheduleWebinar'));
const WebinarMainPage = React.lazy(() => import('../consulting/WebinarMainPage'));
const UpcomingMainPage = React.lazy(() => import('../consulting/UpcomingMainPage'));
const UpcomingDetail = React.lazy(() => import('../consulting/UpComingDetail'));
const BookAppointmentMainPage = React.lazy(() => import('../consulting/bookAppointmentLayout'));
const UserRequestAppointmentDetail = React.lazy(() => import('../consulting/user/RequestDetail'));
const UserFeedback = React.lazy(() => import('../consulting/user/Feedback'));

// Activities
const Activities = React.lazy(() => import('../../user/activities/Activities'));

// Payment
const Payment = React.lazy(() => import('../payment/PaymentMainPage'));
const PaymentInformation = React.lazy(() => import('../payment/Information'));
const TypePayment = React.lazy(() => import('../payment/TypePayment'));
const PaymentDetail = React.lazy(() => import('../payment/PaymentDetail'));
const PaymentSuccess = React.lazy(() => import('../payment/PaymentSuccess'));
const PaymentDirect = React.lazy(() => import('../payment/Subscriptions'));
//const CustomerPortal = React.lazy(() => import('../payment/Billing'));

const Layout: React.FC<RouteComponentProps> = () => {
  const location = useLocation();
  const pathname = location.pathname;
  const groupName = useSelector((state: any) => state.auth.group_name);

  const color = pathname.includes('mobility') || pathname.includes('payment') ? 'violet' : 'petrol';

  return (
    <div className={color}>
      <Suspense fallback={loading}>
        <Switch>
          <LayoutRoute
            path="/newsfeed/intro"
            exact
            header="logo"
            background="fill"
            hideFooter
            component={NewsfeedIntro}
          />
          <LayoutRoute path="/newsfeed" exact component={ViewArticles} />
          <LayoutRoute path="/newsfeed/select-category" exact background="fill" hideFooter component={SelectCategory} />
          <LayoutRoute path="/newsfeed/select-category/search" exact component={SelectCategorySearch} />
          <LayoutRoute path="/newsfeed/favorite" exact component={Favorite} />
          <LayoutRoute path="/mobility/intro" exact background="fill" hideFooter component={MobilityIntro} />
          <LayoutRoute path="/mobility/posture" exact background="fill" hideFooter component={PostureType} />
          <LayoutRoute path="/mobility/search" exact component={MobilitySearch} />
          <LayoutRoute path="/mobility" exact component={MobilityView} />
          <LayoutRoute path="/mobility/watch" exact background="none" component={MobilityDetail} />
          <LayoutRoute path="/mobility/saved" exact component={MobilitySaved} />
          <LayoutRoute path="/mobility/hashtag/:keyword" exact background="none" component={Hashtag} />
          <LayoutRoute path="/mobility/all-trainers" exact component={AllTrainers} />
          <LayoutRoute path="/mobility/trainer-detail/:id" exact component={TrainerDetail} />
          <LayoutRoute path="/payment" exact component={Payment} />
          <LayoutRoute path="/activity" exact component={Activities} />
          <LayoutRoute path="/consulting" exact component={Consulting} />
          <LayoutRoute path="/consulting/book-an-appointment" exact hideFooter component={BookAppointmentMainPage} />
          <LayoutRoute path="/consulting/add-friend" exact component={AddFriendMainPage} />
          <LayoutRoute path="/consulting/search-friend" exact component={SearchFriend} />
          <LayoutRoute path="/consulting/friend-request" exact component={FriendRequest} />
          <LayoutRoute path="/consulting/schedule-webinar" exact component={ScheduleWebinar} />
          <LayoutRoute path="/consulting/information/:userName" exact component={InformationFriend} />
          <LayoutRoute path="/consulting/webinar" exact component={WebinarMainPage} />
          <LayoutRoute path="/consulting/upcomming" exact component={UpcomingMainPage} />
          <LayoutRoute
            path="/consulting/:appointmentType/view-appointments"
            exact
            component={AllAppointmentsMainPage}
          />
          <LayoutRoute
            path="/consulting/:appointmentType/view-appointments/:requestType"
            exact
            component={ViewAppointments}
          />
          <LayoutRoute path="/consulting/upcoming-detail/:id" exact component={UpcomingDetail} />
          <LayoutRoute
            path="/consulting/:appointmentType/view-appointments/:requestType/:id"
            exact
            component={UserRequestAppointmentDetail}
          />
          <LayoutRoute path="/consulting/feedback" exact hideFooter component={UserFeedback} />
          <LayoutRoute path="/nurse-documents" exact background="none" component={NurseDocuments} />
          <LayoutRoute path="/nurse-feedbacks" exact background="none" component={NurseFeedbacks} />
          <LayoutRoute path="/profile" exact component={Profile} />
          <LayoutRoute path="/change-password" exact background="none" component={ChangePassword} />
          <LayoutRoute path="/update-bio" exact background="none" component={UpdateBio} />
          <LayoutRoute path="/payment/information" exact background="none" component={PaymentInformation} />
          <LayoutRoute path="/type-payment" exact background="none" component={TypePayment} />
          <LayoutRoute path="/payment-detail/:id" exact background="none" component={PaymentDetail} />
          <LayoutRoute path="/payment-success" exact background="none" component={PaymentSuccess} />
          <LayoutRoute path="/payment-direct" exact background="none" component={PaymentDirect} />

          <Redirect from="/" to={groupName === 'nurse' || groupName === 'assistant' ? '/consulting' : '/newsfeed'} />
        </Switch>
      </Suspense>
    </div>
  );
};

export default Layout;
