import React from 'react';
import { Route, useLocation } from 'react-router-dom';

const HeaderLogo = React.lazy(() => import('./HeaderLogo'));
const Header = React.lazy(() => import('./Header'));
const Footer = React.lazy(() => import('./Footer'));

interface Props {
  component: (props: any) => any;
  path: string;
  exact?: boolean;
  background?: 'ellipse' | 'fill' | 'none';
  header?: 'full' | 'logo' | 'none';
  hideFooter?: boolean;
}

const LayoutRoute: React.FC<Props> = (props) => {
  const { background = 'ellipse', header = 'full', hideFooter = false, ...rest } = props;
  const location = useLocation();
  const pathname = location.pathname;

  return (
    <div
      className={`hb-layout-wrapper ${background === 'fill' ? 'hb-background' : ''}`}
      style={{ minHeight: window.screen.height < 960 && !hideFooter ? '960px' : '100vh' }}
    >
      <div>
        {header === 'full' ? <Header pathname={pathname} /> : header === 'logo' && <HeaderLogo />}
        {background === 'ellipse' ? (
          <>
            <div className="hb-ellipse">
              <div className="hb-ellipse-children" />
            </div>
            <div className="hb-wrapper">
              <Route {...rest} />
            </div>
          </>
        ) : (
          <Route {...rest} />
        )}
      </div>
      {!hideFooter && <Footer />}
    </div>
  );
};

export default LayoutRoute;
